.todayLabel {
    display: inline-block;
    position: relative;
    height: 32px;
    z-index: 10;

    .todayDate,
    .daysText {
        position: relative;
        font-size: 24px;
        line-height: 1.2;
        height: 32px;
        display: flex;
        align-items: center;
        padding: 5;
        margin: 0 10px;
        z-index: 10;
    }

    .todayDate {
        padding: 5px;
        flex-direction: row;
        gap: 0;
        margin: 0 10px;
        
        .yearInput,
        .monthInput,
        .dayInput {
            appearance: none;
            border: 0;
            background: transparent;
            color: #000;
            font-size: 24px;
            padding: 0 1px;
            pointer-events: auto;
            user-select: text;
            opacity: 1;

            ::placeholder {
                font-size: 10px;
            }
        }
        
        .yearInput {
            width: 60px;

        }

        .yearInput,
        .monthInput {
            margin-right: 16px;
        }

        .monthInput,
        .dayInput {
            width: 30px;
        }
    }
    
    .daysText {
        cursor: pointer;
        padding: 5px;
    }
}
